@import url('https://fonts.googleapis.com/css2?family=Domine&display=swap');
.navbar {
  position: fixed;
  height: 85px;
  width: 100%;
  top: 0;
  color: black;
  display: flex;
}
.title {
  display: flex;
}
.title > div > a {
  text-decoration: none;
  color: black;
  font-size: 20px;
  margin: auto;
  margin-right: 50px;
  border: 2px solid transparent;
  font-weight: bold;
}

.navbar > .contents > a {
  font-size: 20px;
  margin-right: 18px;
  text-decoration: none;
  color: black;
}
.contents {
  display: flex;
}
.contents > .btn {
  border: 2px solid #035473;
  border-radius: 25px;
  padding: 5px 20px;
  background-color: #408ba8;
  font-size: 18px;
  text-align: center;
  margin-right: 40px;
  text-decoration: none;
}
.hoverclass1 {
  padding: 10px;
  border-radius: 25px;
}
.hoverclass1:hover {
  /* background-color: #408ba8; */
  /* color: white; */
  border: 2px solid #035473;
}
.navactive {
  background-color: white;
}
@media only screen and (max-width: 420px) {
  .navbar {
   height: auto;
   padding: 0px;
   padding-top: 15px;
  }
  .title > h3 > .link {
    margin: 0px;
  }

  .contents {
    display: none;
  }
}

.title > h3 {
  display: flex;
  font-size: 30px;
  margin: auto;
  margin-right: 100px;
  margin-left: 24px;
}
.link{
  color: #408ba8;
  font-family: 'Domine', serif;
  font-weight: 900;
}
